import React from 'react';
import styles from './Button.module.scss';

const Button = ({ className, type, label, href, onClick, target, leftIcon, rightIcon }) => {
  return (
    <>
      {type === "anchor" ? (
        <a target={target} className={`${styles.anchor} ${className}`} href={href} onClick={onClick}>
          {leftIcon}
          {label}
          {rightIcon}
        </a>
      ) : type === "input-button" ? (
        <input type="button" className={`${styles.wrapper} ${className}`} value={label} />
      ) : (
        <button className={`${styles.wrapper} ${className}`} type={type} onClick={onClick}>
          {leftIcon}
          {label}
          {rightIcon}
        </button>
      )}
    </>
  );
};

export default Button;
