import React from 'react';

import { experience, strings, education, testimonials } from 'assets/data/data';
import coverPhoto from 'assets/images/photos/cover-photo.jpeg';
import profilePhoto from 'assets/images/photos/kiran.jpeg';
import defaultIcon from 'assets/images/icons/portfolio.png';
import { Button } from 'components';
import { PaperPlaneIcon } from 'assets/icons';
import Slider from 'react-slick';
import styles from './MainProfile.module.scss';

const MainProfile = () => {
  var settings = {
    dots: false,
    arrows: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 10000,
  };
  return (
    <div className={styles.wrapper}>
      <div className={styles['cover-photo']}>
        <img src={coverPhoto} alt={'Cover photo of ' + strings.firstName + ' ' + strings.lastName} />
      </div>
      <div className={styles['basic-details']}>
        <div className={styles.dp}>
          <img src={profilePhoto} alt={'Profile photo of ' + strings.firstName + ' ' + strings.lastName} />
        </div>
        <div className={`d-flex ${styles['info-wrapper']}`}>
          <div className={styles['primary-details']}>
            <h2>
              {strings.firstName + ' ' + strings.lastName}
            </h2>
            <p className={styles.designation}>
              {strings.designation}
            </p>
            <div className='d-flex align-items-center'>
              <p className={styles.address}>
                {strings.address}
              </p>
              <a className={styles.link}>
                Contact info
              </a>
            </div>
            <div className={styles.actions}>
              <Button leftIcon={<PaperPlaneIcon />} className={styles['button-resume']} label="Resume" />
              <Button className={styles['button-message']} label="Message" />
            </div>
          </div>
          <div className={`d-flex flex-column  ${styles['secondary-details']}`}>
            {experience.length > 0 && (
              <div className={`d-flex align-items-center ${styles['main-label']}`}>
                {experience[0].logo ? (
                  <img src={experience[0].logo} alt={'Icon of ' + experience[0].experience} />
                ) : (
                  <img src={defaultIcon} className={styles['default-icon']} alt="Default Icon" />
                )}
                <h4 key={experience[0].id}>
                  {experience[0].organisation}, {experience[0].place}
                </h4>
              </div>
            )}
            {education.length > 0 && (
              <div className={`d-flex align-items-center ${styles['main-label']}`}>
                <img src={education[education.length - 1].logo} alt={'Icon of ' + education[education.length - 1].university} />
                <h4 key={education[education.length - 1].id}>
                  {education[education.length - 1].degree}, {education[education.length - 1].university}
                </h4>
              </div>
            )}
          </div>
        </div>
      </div>
      <div className={styles['section-block']}>
        <h4>
          About
        </h4>
        <p className={styles.description}>
          {strings.detailedDescription}
        </p>
      </div>
      <div className={styles['section-block']}>
        <h4>
          Experience
        </h4>
        {experience.map((expItems) => (
          <div className={`d-flex ${styles.experience}`} key={expItems.id}>
            <div className={styles.logo}>
              {expItems.logo ? (
                <img src={expItems.logo} alt={'Icon of ' + expItems.experience} />
              ) : (
                <img src={defaultIcon} className={styles['default-icon']} alt="Default Icon" />
              )}
            </div>
            <div className={styles['exp-details']}>
              <h5 className={styles.designation}>
                {expItems.designation}
              </h5>
              <p className={styles.organisation}>
                {expItems.organisation}
              </p>
              <p className={styles.year}>
                {expItems.startDate} - {expItems.endDate}
              </p>
              <p className={styles.place}>
                {expItems.place}
              </p>
              <ul className={`d-flex ${styles.skills}`}>
                <li>
                  Skills:
                </li>
                {expItems.skills.map((skill, index) => (
                  <li key={index}>{skill}</li>
                ))}
              </ul>
            </div>
          </div>
        ))}
      </div>
      <div className={styles['section-block']}>
        <h4>
          Education
        </h4>
        {education.slice().reverse().map((eduItems) => (
          <div className={`d-flex ${styles.education}`} key={eduItems.id}>
            <div className={styles.logo}>
              {eduItems.logo ? (
                <img src={eduItems.logo} alt={'Icon of ' + eduItems.education} />
              ) : (
                <img src={defaultIcon} className={styles['default-icon']} alt="Default Icon" />
              )}
            </div>
            <div className={styles['edu-details']}>
              <h5 className={styles.university}>
                {eduItems.university}
              </h5>
              <p className={styles.degree}>
                {eduItems.degree}
              </p>
              <p className={styles.year}>
                {eduItems.graduateYear}
              </p>
            </div>
          </div>
        ))}

      </div>
      <div className={`${styles['rec-block']} ${styles['section-block']}`}>
        <h4>
          Recommendations
        </h4>
        <Slider {...settings}>
          {testimonials.map((testimonialsItem) => (
            <div className={styles['carousel-item']} key={testimonialsItem.id}>
              <div className='d-flex justify-content-between align-items-center'>
                <div className={`d-flex align-items-center ${styles['avatar-info']}`}>
                  <div className={styles.dp}>
                    <img src={testimonialsItem.image} alt='Image of ' />
                  </div>
                  <div>
                    <h4>
                      {testimonialsItem.name}
                    </h4>
                    <h5>
                      {testimonialsItem.designation}, {testimonialsItem.organisation}
                    </h5>
                  </div>
                </div>
              </div>
              <div className={styles['msg-wrapper']}>
                <div className={styles.message}>
                  "{testimonialsItem.message}"
                </div>
              </div>
            </div>
          ))}
        </Slider>
      </div>
      <footer>
        <p>
          {strings.linedinDisclaimer}
          <a>
            {strings.email}
          </a>
        </p>
      </footer>
    </div>
  );
};

export default MainProfile;
