import React from 'react'

import styles from './TextField.module.scss'

const TextField = ({ className, placeholder, type, rows }) => {
    return (
        <div className={`${styles.wrapper} ${className}`}>
            {type === "textarea" ? <textarea placeholder={placeholder} rows={rows}></textarea> : <input type={type} placeholder={placeholder} />}
        </div>
    );
};

export default TextField
