import React, { useState, useEffect } from 'react';
import { Default, VSCode, Linkedin, Google } from './themes';
import ThemeSwitch from 'components/theme-switch/ThemeSwitch';
import { strings } from 'assets/data/data';
import { Header } from 'components';
import { Header as DefaultHeader } from './themes/default/components';
import { Header as LinkedinHeader } from './themes/linkedin/components';
import { Header as GoogleHeader } from './themes/google/components';
import { DefaultIcon, LinkedinColorIcon, VSCodeIcon, GoogleIcon } from 'assets/icons';
import styles from './Landing.module.scss';

const Landing = () => {
    const [theme, setTheme] = useState(() => {
        const savedTheme = localStorage.getItem('selectedTheme');
        return savedTheme || 'default';
    });

    const handleThemeChange = (newTheme) => {
        setTheme(newTheme);
    };

    useEffect(() => {
        localStorage.setItem('selectedTheme', theme);
    }, [theme]);

    const themes = [
        {
            name: 'Default',
            icon: <DefaultIcon />,
            handle: () => handleThemeChange('default'),
            active: theme === 'default'
        },
        {
            name: 'VS Code',
            icon: <VSCodeIcon/>,
            handle: () => handleThemeChange('vscode'),
            active: theme === 'vscode'
        },
        {
            name: 'Linkedin',
            icon: <LinkedinColorIcon />,
            handle: () => handleThemeChange('linkedin'),
            active: theme === 'linkedin'
        },
        {
            name: 'Google',
            icon: <GoogleIcon />,
            handle: () => handleThemeChange('google'),
            active: theme === 'google'
        }
        // Add more themes as needed
    ];

    const renderHeader = () => {
        const themeSwitch = (
            <ThemeSwitch
                className="linkedin-header"
                themes={themes}
            />
        );

        switch (theme) {
            case 'vscode':
                return <Header projectName={strings.projectName} themeSwitch={themeSwitch} />;
            case 'default':
                return <DefaultHeader themeSwitch={themeSwitch} />;
            case 'linkedin':
                return <LinkedinHeader className="linkedin-header" themeSwitch={themeSwitch} />;
            default:
                return <GoogleHeader className="google-header" themeSwitch={themeSwitch} />;
        }
    };

    const renderThemeComponent = () => {
        switch (theme) {
            case 'vscode':
                return <VSCode />;
            case 'default':
                return <Default />;
            case 'linkedin':
                return <Linkedin />;
            default:
                return <Google />;
        }
    };

    return <div className={styles.wrapper}>{renderHeader()}{renderThemeComponent()}</div>;
};

export default Landing;
